.list-group-item {
	padding: 15px 20px;
	border-bottom: 1px solid #3d5a80;
	&:last-child {
		border-bottom: none;
	}
	span {
		line-height: 35px;
		font-size: 20px;
		cursor: pointer;
		min-width: 550px;
	}
	input {
		line-height: 35px;
		font-size: 20px;
		text-align: center;
		border: 0;
	}
	button {
		width: 35px;
		height: 35px;
		margin: 3px;
		font-size: 17px;
		border: none;
		cursor: pointer;
	}
	.btn-cookie {
		color: #e09f3e;
	}
	.btn-trash {
		color: #e5383b;
	}
}

.list-group-item .fa-star {
	width: 35px;
	height: 35px;
	text-align: center;
	line-height: 35px;
	font-size: 16px;
	color: #ffd700;
	transition: 0.3s all;
	transform: translateX(30px);
	opacity: 0;
}

.list-group-item.like .fa-star {
	opacity: 1;
	transform: translateX(0px);
}

.list-group-item.increase .list-group-item-label,
.list-group-item.increase .list-group-item-input {
	color: #e09f3e;
}

.list-group-item.increase .btn-star {
	color: #aeaeae;
}
